@import './config.scss';

//info-workspace wrap 
.info-WS-wrap{
    font-family:'Source Code Pro',sans;
}

//about
.info-about-wrap{
    @include centerScreen;
    @include whSizing(100%,70%);
    color: $mainFontStyle;
    justify-content: flex-end;
    flex-direction: column;
    display: flex;
    z-index: -100;
    .info-about-p-wrap{
        width: 1000px;
        div{
            font-size: 2.8em;
            line-height: 1.4;
            margin: 0 auto;
            width: 1000px;
            @include centerScreen;
        }
        h1{
            margin: 0;
            font-size: 53px;
            margin-bottom: 35px;
            color: #82a381;
        }
    }

}

//Instructions
.info-instructions-wrap{
    color: $mainFontStyle;
    margin-top: 30px;
    //width: 650px;
    max-width: 100%;
    z-index: -500;
    @include centerScreen();

    form.info-instructions-btn-wrap{
        @include justifyFlex(space-between);
        margin-top: 30px;
        // button.info-instructions-btn{
            
        // }
    }
    .info-instructions-article-wrap{
        height: 537px;
        //margin: 0px 25px;
        position: relative;

        .info-instructions-article{
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            height: 100%;
            position: absolute;
        }
    }
}

//info footer
footer.info-about{
    //align-self: center;
    margin-bottom: 10px;
    padding: 0 15px;
    position: fixed;
    bottom: 0;
    box-sizing: border-box;
    width: 100%;
    ul{ //footer children
        //width: 500px;
        flex-direction: row;
        justify-content: space-between;
        display: flex;
        flex-wrap: wrap;
        padding: 0;
        margin-bottom: 0;

        li:nth-child(1){ //website link
            display: inline-block;
            align-self: center;
            a{
                color: #82a381;
                list-style: none;   
                padding-left: 7px;
                transition: $stndrdTransition;
                &:hover{
                    color:#95bd94;
                    transition: $stndrdTransition;
                }
            }
        }
        li:nth-child(2){ //social links
            display: inline-block;
            align-self: center;
        }
    }
}


//Ipad and mobile
@media screen and (max-width: 1000px){
    //about
    .info-about-wrap{
        .info-about-p-wrap{
            box-sizing: border-box;
            height: 100%;
            margin: 0px 23px;
            position: absolute;
            width: auto;
            z-index: -100;
            div{
                // font-size: 2em;
                position: relative;
                width: 100%;
            }
        }
    } 
    //footer
    footer.info-about{
        margin-bottom: 10px;
    }

    .info-instructions-wrap{
        .info-instructions-btn-wrap{
            /* border: 2px solid red; */
            display: flex;
            width: 300px;
            z-index: 10000 !important;
            .av, .vision, .audio{
                span{
                    padding: 0 !important;
                }
            }
        }
        .info-instructions-article-wrap{
            .av{
                margin-top: -60px;
            }
            .vision{
                .instructions-blob-wrap.gs-rgb{
                    margin: 0 !important;
                }
            }
        }
    }

}


//only mobile
@media screen and (max-width: 450px){
    //about
    .info-about-wrap{

        .info-about-p-wrap{
            div{
                font-size: 1.3em !important;
                line-height: 1.7;
            }
            h1{
                font-size:40px;
            }
        }

    } 

    //instructions
    .info-instructions-wrap{
        margin-top: 0px;
        .info-instructions-article-wrap{
            .info-instructions-article{
                .sg{
                    margin:0 !important;
                }
            }
            .av{
                height: 500px;
                /* border: 2px solid #95bd94; */
                margin-top: 0px;
                overflow: auto;
                justify-content: end;
                .instructions-blob-wrap{
                    padding: 0px 5px;
                }
            }
            .vision{
                .gs-rgb{
                    margin:0 !important;
                }
            }
        }
    }

    //footer
    footer.info-about{
        z-index: 1 !important;
        ul.about-icon-ul{
            justify-content: center;
            margin-top: 0;

            li:nth-child(2){
                display: none;
                padding-top: 20px;
            }
        }
    }
}

@media screen and (max-height: 657px){
    footer.info-about{
        display: none !important;
    }
}