@import './config.scss';

.Audio-WS-Wrap{
    @include centerScreen();
    .audio-tool-wrap {
        margin-top: 30px;
        position: relative;
        z-index: -100;
        //@include centerScreen();
        //signal generator
        .audio-ctrl{
            align-items: center;
            display: flex;
            position: relative;
            padding: 0px 97px;
            justify-content: space-between;
            top: 20px;
            //range is also in here but bc its too big its note 
            button.play-stop-btn{
                font-size: 1.4rem;
                height: 45px;
                letter-spacing: 3px;
                margin-bottom: 13px;
                width: 125px;
                transition: $stndrdTransition;
                &:hover{
                    transition: $stndrdTransition;
                }
            }
        }
}


    .audio-sg-wrap.tool-element-wrap{
        width: 650px;
        height: 556.72px;

        .audio-controls-wrap{
            @include flexDirection(column);

            .sg-header.audio-header{
                @include headerStyle();
            }

            .audio-banner-wrap{
                @include flexDirection(row);
                // @include btnColor();
                @include bannerStyle();

                .audio-control-btn{
                    @include center();
                    @include flexDirection(column);
                    cursor: pointer;
                    transition: $stndrdTransition;
                    width: 142px;
                    -webkit-tap-highlight-color: transparent;

                    .audio-control-icon-wrap {
                        border: 5px solid black;
                    }
                    .audio-control-icon.cos { //constant or sweep
                        width: 75px !important;
                        height: 25px !important;
                        padding-top: 15px;
                        transition: $stndrdTransition;
                        &:hover{
                            transition: $stndrdTransition;
                        }
                    }
                    .audio-control-icon.low-to-high{
                        transform: scaleX(-1);
                    }
                    .audio-control-icon{
                        font-size: 1.5rem;
                        vertical-align: bottom;
                        width: 50px;
                    }
                    .audio-control-label{
                        cursor: pointer;
                    }
                    .audio-control-label.cos{
                        margin-top: 8px;
                    }
                }
                .audio-control-btn.off{
                    opacity: /*25%*/.25;
                    &:hover{
                        opacity: /*1000%*/1;
                    }
                }
                .audio-control-btn.on{
                    opacity: /*100%*/1;
                }
            }
        }
    }
    //recording playback
    .audio-rpb-wrap.tool-element-wrap{
        height: 556.72px;
        justify-content: center;
        display: flex;
        flex-direction: column;

        .audio-controls-wrap.music{
            margin-bottom: 100px;
        }

        .audio-controls-wrap.human-voice, .audio-controls-wrap.music{
            .audio-header.rpb-header{
                @include headerStyle();
                font-size: 2em;
            }
            .audio-banner-wrap.rpb{
                @include bannerStyle;
                height: 128px;

                .btn-rpb-wrap{
                    @include center();

                    .icon-wrap.rpb{
                        @include flexDirection(column);
                        transition: $stndrdTransition;
                        -webkit-tap-highlight-color: transparent;
                        &:hover{
                            opacity: /*100%*/1 !important;
                            transition: $stndrdTransition;
                        } 
                        .rpb.icon-label{
                            cursor:pointer;
                            -webkit-tap-highlight-color: transparent;   
                        }
                        .btn-pb{
                            @include center();
                            border: 5px solid black;
                            border-radius: 10px;
                            // width: 37px;
                            // height: 37px;
                            span.off{
                                display: none;
                            }
                            span.on{
                                display: block;
                            }
                        }
                    }

                    .icon-wrap.rpb.off{
                        opacity: /*25%*/ .25;
                    }

                    .icon-wrap.rpb.on{
                        opacity: /*100%*/1;
                    }
                }
            }
        }
    }

    .audio-test-type-wrap{
        display: flex;
        justify-content: space-around;
        margin-top: -30px;

        .audio-test-type-btn{
            letter-spacing: .8px;
            transition: $stndrdTransition;
            &:hover{
                //@if width >= 1000px
                transition: $stndrdTransition;
            }
        }
    }
}

//RESPONSIVE STYLES
@media screen and (max-width: 1000px){
    .audio-sg-wrap.tool-element-wrap {
        width: auto !important;
    }
    .audio-banner-wrap{
        font-size: .87rem;
    }
    .audio-control-label.frequency{
        padding: 0px 10px;
    }
    // .audio-control-btn:hover{
    //     opacity: /*25%*/.25 !important;
    // }
    .audio-test-type-wrap{
        margin-top: 0px !important;
        margin-bottom: 10px;
        top: -40px;
        position: relative;
    }
    .audio-control-label.constant.cos{
        font-size: 13px;
    }
    .audio-control-btn.off{
        opacity: .25 !important;
    }
    .rpb.icon-wrap.off{
        opacity: .25;
    }

    button.audio-test-type-btn.off,button.play-stop-btn.off{
        opacity: 1;
        filter: brightness(.7);
    }
    button.audio-test-type-btn,button.play-stop-btn{
        filter: brightness(1);
    }
    .audio-tool-wrap {
        .audio-ctrl{
            padding: 0px 42px !important;
        }
    }
}
//mobile
@media screen and (max-width: 450px){
    .Audio-WS-Wrap{
        width: 100%;
    }
}

