@import './config.scss';

//generic classes
.AV-WS-Wrap{
    position: fixed;
    z-index: -1200;
    @include whSizing(100%,100%);

    .av-tool-wrap{

        .iframe-container{
            .av-tool{
                @include centerScreen();
                z-index: -500;
            }
        }
        .av-fullscreen{
            @include whSizing(100vw,100vh);
        }
    }
}

@media screen and (max-width: 1000px){
    .iframe-container{
        overflow: hidden;
        padding-top: 56.25%;
        position: relative;
        .av-tool{
            height: 70%;
            position: absolute;
            width: 100%;
        }
    }
}
