@import './config.scss','./navBar.scss','./dwNav.scss','./audioTool.scss','./visionTool.scss', './audio&vision.scss','./info_about.scss','./rangeStyles.scss';

//Global
html{
    width: 100%;
    height: 100%;
    position: relative;
}

body {
    background-color: #1d1d1d;
    height: 100%;
    font-family: 'orbitron', monospace;
    position: fixed;
    letter-spacing: 1px;
    width: 100%;
    user-select: none;
}
li {
    color: $mainFontStyle;   
    cursor: pointer;
    text-decoration: none;
    text-decoration-color: none;
    transition: all .15s ease-in-out;
    &:hover{
        @extend a;
    }
    &:visited{
        color: $mainFontStyle;   
    }
}

div,li,a,ul,span,button {
    outline: none;
    list-style-type: none;
    &:focus{
        outline: none;
    }
}
.display-none{
    display: none !important;
}

img {
    width: 50px;
    height: 50px;
}

.pointer {
    cursor: pointer;
}

.z500{
    z-index: 500;
}

.disabled {
    pointer-events: none !important;
    opacity: .25 !important;
    transition: $stndrdTransition !important;
}

//buton styles
    button{
        background: $btnColor;
        border-radius: 5px;
        border: none; 
        cursor: pointer;
        font-family:'orbitron', monospace;
        font-weight: 700;
        height: 32px;
        opacity: .5;
        padding: 0px;
        //transition: $stndrdTransition;
        width: 175px;
        -webkit-tap-highlight-color: transparent;
        &:hover{
            opacity: 1;
            //transition: $stndrdTransition;
        }
    }
    button :nth-child(1){
        padding-left: 3.4px;
    }

    button.on{
        opacity: 1 !important;
    }
    button.off{
       // opacity: .25 !important;
    }


@media screen and (max-width: 650px){ 
    .audio-tool-wrap,.audio-controls-wrap,.audio-banner-wrap{
        width: 100% !important;
    }
}