@import './config.scss';

//NAVBAR STYLES


header {
    display: flex;
    width: 100%;
}

#site-logo{
    align-self: start;
    color: rgb(151, 189, 135);
    display: flex;
    font-size: 2rem;
    left: 20px;
    position: fixed;
    top: 10px;
    visibility: hidden;
}

#main-nav-wrap{
    color: $mainFontStyle;
    display: flex;
    height: auto;
    justify-content: center;
    justify-self: center;
    position: fixed;
    transition: all .45s ease;
    width: 100%;
    z-index: 3000;

    nav {
        background: $divBGOne;
    }

    .main-nav-link{
        //padding: 15px;
        transition: all .15s ease-in-out;
        &:hover{
            @include prmryHoverStyle();
        }
    }

}

.selected {
    border-bottom: 2px solid $linkFontColor;
    color:#a2d6a1 !important;
    text-shadow: 2px 0px 5px #17e614d1;
}
.selectedImportant{
    border-bottom: 2px solid $linkFontColor !important;
    color:#a2d6a1 !important;
    text-shadow: 2px 0px 5px #17e614d1 !important;
}

.unselected {
    border-bottom: 2px solid transparent;    
}

.rBZoid { 
    border-top: 72px solid #1c2c1d;
    border-left: 25px solid transparent;
    border-right: 60px solid transparent;
    /* height: 0; */
    width: 125px;
    z-index: -1 !important;
    position: absolute;
    right: -60px;
    height: 72px;
}

.rSZoid {
    border-top: 22px solid #283a2b;
    border-left: 20px solid transparent;
    border-right: 20px solid transparent;
    width: 150px;
    z-index: -1 !important;
    position: absolute;
    right: -80px;
}

#dsktp-nav-lst {
    display: flex;
    width: 720px;
    justify-content: space-around;
    padding: 0;
}

//adaptive nav styles
//...................................................................
    #adaptive-nav-wrap{
        @extend #main-nav-wrap;
        display: none;
        height: 100%;
        width: 100%;
        position: absolute;
        right: -261px;
        z-index: 3000px;
        -webkit-tap-highlight-color: transparent;
        //transition: all .45s ease;

        // &.closed{
        //     display: none !important;
        // }
        // &.opened{
        //     display: flex !important;
        // }
    }

    #adaptive-nav {
        position: absolute;
        right: 0px;
        width: 260px;
        min-height: 500px;
        border-bottom: 13px solid #283a2b;
        border-radius: 0px 0px 0px 15px
    }

    .adptv-nav-tab{
        @extend .rSZoid;

        border-top: 80px solid #1c2c1d !important; //orig 50px
        border-left: 58px solid transparent !important; //orig 50px
        border-right: 58px solid transparent !important; //orig 50px
        display: none;
        position: fixed;
        //right: 183px;
        right: -120px;
        transform: rotate(90deg);
        transition: all .45s ease;
        top: 34.4px; //65px; //orig 135px
        //right: 165px !important;
        z-index: 5000 !important;
        width: 105px;
        }

    #adptv-nav-lst{
        align-items: center;
        flex-direction: column;
        height: 100%;
        margin-top: 40px;
        padding: 0px;
        position: absolute;
        top: 5px;
        width: 100%;
    }

//MEDIA QUERIES
//...................................................................
    @media screen and (max-width: 1000px){
        #main-nav-wrap{
            display: none;
            .main-nav-link{
                padding: 0px;
                &:hover{
                    color: $mainFontStyle;
                }
            }
        }
        #adaptive-nav-wrap{
            display: flex;
            width: 2px;
            z-index: 5000;
        }
        .navChildWrap{
            padding: 10px 10px;
        }
        .adptv-nav-tab{
            display: block;
        }
    }