//fonts
@import url("https://fonts.googleapis.com/css?family=Orbitron&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@500&family=Source+Code+Pro:ital,wght@1,300;1,400&display=swap");

//variables
$mainFontStyle: rgb(218, 218, 218);
$secondaryFontStyle: rgb(162, 214, 161);
$divBGOne: rgb(28, 44, 29);
$divBGTwo: #283a2b;
$linkFontColor: #17e614;
$btnColor: #759776;
$stndrdTransition: all .15s ease-in-out;

//mixins
@mixin center(){
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin centerScreen(){ // center content to middle of screen
    left: 50%;
    position: fixed;
    top: 50%;
    transform: translate(-50%, -50%);
}

@mixin flexDirection($direction){
    display: flex;
    flex-direction: $direction;
}

@mixin justifyFlex($justify,){
    display: flex;
    justify-content:$justify;
}

@mixin prmryHoverStyle(){
    color: $secondaryFontStyle;
    transition: $stndrdTransition
}

// @mixin btnColor(){
//     opacity: 80%;
//     background: #759776;
// }

@mixin headerStyle(){
    color: white;
    margin-bottom: 10px;
}

@mixin bannerStyle(){
    display: flex;
    background: #283a2bc2;
    color: #b9eab8;
    height: 100px;
    justify-content: space-around;
    width: 650px;
}

@mixin whSizing($width, $height){
    width: $width;
    height: $height;
}

