@import './config.scss';

//Vision tools parent wrap
.vision-WS-wrap{

    //all of the vision tools
    .vision-greyscale-wrap,.vision-RGB-wrap,.vision-solid-wrap{
      z-index: 3000;      
    }

    //greyscale
    //...................................
    .vision-greyscale-wrap{
        display: flex;
        height: 100vh;
        justify-content: start;
        position: fixed;
        width: 100vw;
        z-index: -100;

        .greyscale-vertical-wrap{
            @include flexDirection(column);
            @include whSizing(100%,100%);

            .greyscale-vertical{
                // width: 100%;
                // height: 50%;
                //z-index: -100;
                @include whSizing(100%,50%)
            }
            .greyscale-vertical.top, .greyscale-left{
                background: linear-gradient(91deg, black, white);
            }
            .greyscale-vertical.bottom, .greyscale-right{
                background: linear-gradient(91deg, white, black);  
            }
        }

        .greyscale-fullscreen-wrap{
            @include flexDirection(row);
            @include whSizing(100%,100%);
            position: absolute;

            .greyscale-fullscreen{
                @include whSizing(100%,100%);
                background: linear-gradient(91deg, black, white);
            }
        }
    }

    //RGB
    //...............................
    .vision-RGB-wrap{
        @extend .vision-greyscale-wrap;
        .rgb-node{
            flex-basis: 20%;
        }
    }

    //SOLID COLOR
    //...............................
    .vision-solid-wrap{
        @extend .vision-greyscale-wrap;
        .solid-node{
            flex-grow: 1;
        }
    }

}

//mobile
@media screen and (max-width: 450px){
    .vision-tool{
        z-index: 200 !important;
    }
}
