@import './config.scss';
@import './navBar.scss';

.drp-dwn-nav-wrap{
    align-self: flex-start;
    position: absolute;
    top: 60px;
}

.drp-dwn-nav{
    align-items: baseline;
    align-self: flex-start;
    background-color: #283a2b;
    display: flex;
    flex-direction: column;
    font-size: 1.2rem;
    min-height: auto;
    margin-top: 12px;
    position: fixed;
    min-width: 160px;
    padding: 5px 20px 5px 20px;
    z-index: -1;
    justify-content: space-around;

    a {
        transition: all .15s ease-in-out;
        &:hover{
            @include prmryHoverStyle();
        }
    }
}

.drp-dwn-child{
    padding: 5px 0px 5px 0px;
    text-align: left;
}

//MEDIA QUERIES
//...................................................................
@media screen and (max-width: 1000px){

    .drp-dwn-nav-wrap{
        position: relative !important;
        top: 0px !important;
        z-index: 500 !important;
    }
    .drp-dwn-nav{
        font-size: .99rem;
        position: relative;
    }
}